import "./App.css";
import React from "react";
import { DataProvider } from './components/DataContext';
import { BrowserRouter as Router, Routes, Route, Link } from "react-router-dom";
import "@arco-design/web-react/dist/css/arco.css";
import IndustrySelectForm from "./components/IndustrySelectForm";
import LanguageSelectForm from "./components/LanguageSelectForm";
import FeaturesSelectForm from "./components/FeaturesSelectForm";
import RecommendationPage from "./components/ReccomendationPage";
import recommendationsData from "./components/recommendations.json";

function App() {
  return (
    <DataProvider>
      <Router basename="/esp-fitting-room">
        <div>
          <Routes>
            <Route path="/select-industry" element={<IndustrySelectForm />} />
            <Route path="/select-language" element={<LanguageSelectForm />} />
            <Route path="/select-features" element={<FeaturesSelectForm />} />
            <Route
              path="/reccomendations"
              element={
                <RecommendationPage recommendations={recommendationsData} />
              }
            />
            <Route path="/" element={<Home />} />{" "}
            {/* Define your Home component or use another component */}
          </Routes>
        </div>
      </Router>
    </DataProvider>
  );
}

function Home() {
  return <IndustrySelectForm />;
}

export default App;
