    function filterData(selectedData, dataToCheck) {
      const { features, industry, language } = selectedData;
    
      const matchingChips = dataToCheck.chips.filter(chip =>
        chip.applications.includes(industry) &&
        features.every(feat => chip.features.includes(feat)) &&
        language.every(lang => chip.supportedLanguages.includes(lang))
      );
    
      const matchingSdks = dataToCheck.sdks.filter(sdk =>
        sdk.applications.includes(industry) &&
        features.every(feat => sdk.features.includes(feat)) &&
        language.some(lang => sdk.supportedLanguages.includes(lang))
      );
    
      const matchingLibraries = dataToCheck.libraries.filter(library =>
        features.every(feat => library.features.includes(feat))
      );
    
      return {
        chips: matchingChips,
        sdks: matchingSdks,
        libraries: matchingLibraries
      };
    }

